/* Color pallette */

.unused-dark {
  background-color: #2e2a2c; /* Background color */
  background-color: #2b2e2a; /* Componenet background color */
  background-color: #201d1f; /* Font outline color */
  background-color: #fff9fc; /* Text color */
  background-color: #757575; /* Text focused color */
  background-color: #1e201d; /* Component outline color*/
}

.unused-light {
  background-color: #fff9fc; /* Background color */
  background-color: #dad8d7; /* Comonenet background color */
  background-color: #c2c0bf; /* Font outline color */
  background-color: #2b2e2a; /* Text color */
  background-color: #757575; /* Text focused color */
  background-color: #1e201d; /* Component outline color*/
}

/* Root element */

#root > div {
  height: 100vh;
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

/* Fonts */

@font-face {
  font-family: quicksand;
  src: url(../public/fonts/quicksand/Quicksand_Book.otf);
}

@font-face {
  font-family: quicksand-bold;
  src: url(../public/fonts/quicksand/Quicksand_Bold.otf);
}

@font-face {
  font-family: josefinSans-light;
  src: url(../public/fonts/Josefin_Sans/static/JosefinSans-Light.ttf);
}

@font-face {
  font-family: josefinSans;
  src: url(../public/fonts/Josefin_Sans/static/JosefinSans-Regular.ttf);
}

@font-face {
  font-family: josefinSans-bold;
  src: url(../public/fonts/Josefin_Sans/static/JosefinSans-Bold.ttf);
}

.hover-dim:hover {
  background-image: linear-gradient(rgb(0 0 0/40%) 0 0);
}

.hover-trans:hover {
  background-color: rgba(0, 0, 0, 0);
}

/* Dark mode settings */

.background-dark {
  background: #2e2a2c;
}

.component-background-dark {
  background: #2b2e2a;
}

.component-outline-dark {
  border: 2px solid #1e201d;
}

.component-outline-background-dark {
  background: #1e201d;
}

.title-font-dark {
  font-family: josefinSans-bold, sans-serif;
  text-shadow: -1px -1px 0 #201d1f, 1px -1px 0 #201d1f, -1px 1px 0 #201d1f,
    1px 1px 0 #201d1f;
  color: #fff9fc;
}

.font-dark {
  font-family: josefinSans-bold, sans-serif;
  text-shadow: -1px -1px 0 #201d1f, 1px -1px 0 #201d1f, -1px 1px 0 #201d1f,
    1px 1px 0 #201d1f;
  color: #fff9fc;
}

.font-dark-no-outline {
  font-family: josefinSans, sans-serif;
  color: #fff9fc;
}

.icon-dark {
  font-family: josefinSans-bold, sans-serif;
  text-shadow: -1px -1px 0 #201d1f, 1px -1px 0 #201d1f, -1px 1px 0 #201d1f,
    1px 1px 0 #201d1f;
  color: #fff9fc;
}

.searchbar-font-dark {
  font-family: josefinSans-bold, sans-serif;
  text-shadow: -1px -1px 0 #201d1f, 1px -1px 0 #201d1f, -1px 1px 0 #201d1f,
    1px 1px 0 #201d1f;
  color: #757575;
}

.searchbar-font-dark:focus {
  color: #fff9fc;
}

.footer-icon-dark {
  color: #dad8d7;
}

/* Light mode settings */

.background-light {
  background: #f3f1ef;
}

.component-background-light {
  background: #dad8d7;
}

.component-outline-light {
  border: 2px solid #1e201d;
}

.component-outline-background-light {
  background: #1e201d;
}

.title-font-light {
  font-family: josefinSans-bold, sans-serif;
  text-shadow: -1px -1px 0 #201d1f, 1px -1px 0 #201d1f, -1px 1px 0 #201d1f,
    1px 1px 0 #201d1f;
  color: #fff9fc;
}

.font-light {
  font-family: josefinSans-bold, sans-serif;
  color: #2b2e2a;
}

.icon-light {
  font-family: josefinSans-bold, sans-serif;
  color: #fff9fc;
}

.searchbar-font-light {
  font-family: josefinSans-bold, sans-serif;
  color: #757575;
}

.searchbar-font-light:focus {
  color: #2b2e2a;
}

.footer-icon-light {
  color: #2b2e2a;
}

/* Common settings */

.clean-text {
  text-decoration: none;
}

/* Loading components */

.loading-ball-container {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
}

/* Home page layout */

.homeview-container {
  min-height: 100vh;
  min-width: 100vw;
  display: grid;
  place-items: center;
  grid-template-rows: auto auto auto auto 1fr auto; /* Title, Search, Types, Sort, Poké Table, footer */
  gap: 20px 0px;
}

.homeview-small {
  padding: 5% 2% 0 2%;
}

.homeview-med {
  padding: 5% 5% 0 5%;
}

.homeview-large {
  padding: 5% 10% 0 10%;
}

/* Home page header */

.header {
  text-align: center;
}

.header-small {
  padding: 1rem;
  font-size: 2.5rem;
}

.header-med {
  padding: 1rem;
  font-size: 4rem;
}

.header-large {
  padding: 1.5rem;
  font-size: 5rem;
}

.header-x-large {
  padding: 1.5rem;
  font-size: 7rem;
}

/* Home page sub-header */

.sub-header {
  padding: 2rem 0 1rem 0;
  text-align: center;
  font-size: 2rem;
}

/* Search bar */

.searchbar-container {
  display: flex;
  position: relative;
  width: 350px;
}

.searchbar-input {
  width: 100%;
  padding: 12px 20px;
  border-right: none;
  border-radius: 40px 0 0 40px;
  outline: none;
  font-size: 1rem;
}

.searchbar-search-icon {
  width: 55px;
  height: 100%;
  border-radius: 0 40px 40px 0;
  border: none;
  text-align: center;
  padding: 0 5px 0 0;
  font-size: 1rem;
}

.search-results {
  display: none;
  position: absolute;
  overflow: hidden;
  overflow-y: auto;
  height: auto;
  max-height: 250px;
  width: 273px;
  text-align: center;
  top: 100%;
  left: 50%;
  margin-left: -150px;
  border-radius: 5px;
  border-top: none;
  box-shadow: 0px 8px 16px 0px rgba(0, 0, 0, 0.2);
  padding: 6px 0;
  z-index: 1;
}

.search-results-active {
  display: block;
}

.search-results-item {
  padding: 12px 16px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.search-result-image {
  height: 42.5px;
  width: 42.5px;
  object-fit: contain;
  margin: 0 10px 0 0;
}

.search-results-item:hover {
  background-image: linear-gradient(rgb(0 0 0/40%) 0 0);
}

/* Types Table */

.types-table {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  gap: 10px 15px;
  padding: 20px 10px;
}

.type-item {
  display: flex;
  flex-wrap: nowrap;
  font-size: 0.75rem;
  text-decoration: none;
  padding: 0.5rem;
  border-radius: 8px;
}

.type-img {
  height: 20px;
  width: 20px;
  margin: 0 7px 0 0;
}

.type-text {
  padding: 3px 0 0 0;
}

/* Types dropdown */

.types-dropdown-container {
  display: flex;
  position: relative;
  width: 200px;
}

.types-dropdown-button {
  width: 100%;
  padding: 15px 0;
  outline: none;
  border-radius: 40px;
  text-align: center;
  font-size: 1rem;
}

.types-dropdown-content {
  display: none;
  position: absolute;
  overflow: hidden;
  overflow-y: auto;
  height: auto;
  max-height: 200px;
  border-radius: 5px;
  border-top: none;
  box-shadow: 0px 8px 16px 0px rgba(0, 0, 0, 0.2);
  width: 160px;
  left: 50%;
  margin-left: -80px;
  padding: 6px 0;
  text-align: center;
  z-index: 1;
}

.types-dropdown-content-active {
  display: block;
}

.types-dropdown-item {
  padding: 12px;
  text-decoration: none;
  display: flex;
  justify-content: start;
  align-items: center;
}

.types-dropdown-img {
  height: 24px;
  width: 24px;
  object-fit: contain;
  margin: 0 16px 0 0;
}

