/* Sort options */

.sortoptions-container-small {
  display: flex;
  padding: 10px;
  width: 100%;
  gap: 10px 0;
  flex-direction: column;
  justify-content: center;
}

.sortoptions-container-med-large {
  display: flex;
  margin: 10px;
  width: 100%;
  gap: 0 10px;
  justify-content: center;
}

.sortoptions-item {
  display: flex;
  flex-direction: column;
  overflow-y: visible;
  width: auto;
}

.sortoption-text {
  padding: 0.5rem;
  text-align: center;
  font-size: 1rem;
}

.sortoptions-dropdown {
  place-self: center;
  width: 150px;
  height: 25px;
  border-radius: 5px;
  text-align: center;
  border: none;
  outline: 1px solid #1e201d;
  cursor: pointer;
}

/* Pokemon display table */

.pokemon-container-small {
  display: grid;
  gap: 20px 20px;
  padding: 1rem;
  grid-template-columns: repeat(2, minmax(0, 1fr));
  grid-auto-rows: minmax(0, 1fr);
}

.pokemon-container-med {
  display: grid;
  gap: 20px 20px;
  padding: 1.5rem;
  grid-template-columns: repeat(3, minmax(0, 1fr));
  grid-auto-rows: minmax(0, 1fr);
}

.pokemon-container-large {
  display: grid;
  gap: 20px 20px;
  padding: 2rem;
  grid-template-columns: repeat(4, minmax(0, 1fr));
  grid-auto-rows: minmax(0, 1fr);
}

.pokemon-container-xlarge {
  display: grid;
  gap: 20px 20px;
  padding: 2rem;
  grid-template-columns: repeat(6, minmax(0, 1fr));
  grid-auto-rows: minmax(0, 1fr);
}

.pokemon-card {
  flex-wrap: nowrap;
  flex-direction: column;
  padding: 1rem;
  border-radius: 5px;
  text-decoration: none;
  outline: 1px solid #1e201d;
  height: 100%;
}

.pokemon-card:hover {
  box-shadow: 0 8px 16px 0 #201d1f;
}

.pokemon-card-title {
  padding: 0 0 1rem 0;
  font-size: 1rem;
}

.pokemon-image {
  padding: 0.5rem;
  width: 100%;
  height: 100%;
  object-fit: contain;
}

.pokemon-load-image {
  padding: 2.5rem;
  width: 100%;
  height: 100%;
  object-fit: contain;
}

.pokemon-card-number {
  align-self: flex-end;
  padding: 1rem 0 0 0;
  font-size: 0.75rem;
}

/* Footer */

.footer-container {
  display: grid;
  width: 100%;
  gap: 10px 10px;
  padding: 10px;
  align-items: center;
  grid-template-columns: 1fr 1fr 1fr;
  grid-template-rows: auto;
  grid-template-areas:
    "github developed api"
    "trademark trademark trademark";
  font-size: 1rem;
}

.footer-item-1 {
  width: 100%;
  grid-area: github;
}

.github-logo {
  font-size: 1.5rem;
}

.footer-item-2 {
  width: 100%;
  grid-area: developed;
  text-align: center;
}

.footer-item-3 {
  width: 100%;
  text-align: end;
  grid-area: api;
}

.api-logo {
  height: 1.5rem;
  padding: 0 8px;
  box-sizing: border-box;
  margin-bottom: 5px;
}

.footer-item-4 {
  width: 100%;
  text-align: center;
  grid-area: trademark;
}

/* Type page layout */

.typeview-container {
  min-height: 100vh;
  min-width: 100vw;
  display: grid;
  place-items: center;
  grid-template-rows: auto auto 1fr auto;
  gap: 20px 0px;
}

.typeview-small {
  padding: 0 2%;
}

.typeview-med {
  padding: 0 5%;
}

.typeview-large {
  padding: 0 10%;
}

/* Secondary page title */

.secondary-header-container {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  padding: 0 0 2rem 0;
}

.secondary-header-container-small {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  padding: 0 0 1.5rem 0;
}

.secondary-header {
  text-align: center;
}

.secondary-header-small {
  margin: 1rem 0;
  font-size: 2rem;
}

.secondary-header-med {
  margin: 1rem 0;
  font-size: 1.5rem;
}

.secondary-header-large {
  margin: 1.5rem 0;
  font-size: 3rem;
}

.secondary-header-x-large {
  margin: 1.5rem 0;
  font-size: 4rem;
}

.secondary-searchbar-container {
  justify-self: start;
}

/* Not found page layout */

.notfound-container {
  min-height: 100vh;
  min-width: 100vw;
  display: grid;
  place-items: center;
  grid-template-rows: auto auto 1fr auto;
  gap: 20px 0px;
}

.notfound-small {
  padding: 0 2%;
}

.notfound-med {
  padding: 0 5%;
}

.notfound-large {
  padding: 0 10%;
}

.notfound-image {
  padding: 0.5rem;
  width: 250px;
  height: 250px;
  object-fit: contain;
}
